.Intro {
    margin-bottom: 25px;
}

.IntroP {
    font-weight: bold;
    margin-bottom: 5px;
    color: gray;
    line-height: 24px;
}

.IntroSkills {
    font-size: 15px;
    margin: 5px auto;
    line-height: 24px;
}

.TechUsedDiv {
    display: -ms-flexbox;
    display: flex;
}

.CardContainer {
    position: relative;
    margin: 25px auto;
    width: 100px;
    height: 100px;
    z-index: 1;
    -webkit-perspective: 10000;
            perspective: 10000;
}

.Card {
    width: 100%;
    height: 100%;
    -webkit-transform-style: preserve-3d;
            transform-style: preserve-3d;
    -webkit-transition: all 1.0s linear;
    -o-transition: all 1.0s linear;
    transition: all 1.0s linear;
    vertical-align: middle;
}

.CardContainer:hover .Card {
    -webkit-transform: rotateY(180deg);
            transform: rotateY(180deg);
}

.Side {
    position: absolute;
    width: 100%;
    height: 100%;
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
}

.Side img {
    min-width: 100%;
    min-height: 100%;
}

.Side .Back {
    display: block;
    -webkit-transform: rotateY(180deg);
            transform: rotateY(180deg);
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    color: #000000;
    text-align: center;
    background-color: white;
}

.Exhb {
    position: relative;
    top: 40%;
}

.MatUI {
    position: relative;
    top: 25%
}
@media only screen and (max-width: 767px) {
    .ui.container.LogosWrapper {
        margin: auto !important;
    }

}


